/**
 * pop 
 *
 * @props headerName - 
 * @props message - alert
 * @props okBtnName - 
 * @props noBtnName - 
 * @props noBtnDisplay - 
 * @props headerNoBtnDisplay - X
 * @props headerDisplay - 
 * @props footerDisplay - 
 * @props type - 
 *
 * @slot - 
 *
 * @events ok - 
 * @events no - 
 *
 */

import './pop.scss'
import './pop.m.scss'

import render from './pop.render'
import btnComp from 'src/component/base/btn/btn'
import iconComp from 'src/component/base/icon/icon'
import baseMixin from 'src/mixin/base'

const TYPE_ALERT = 'alert'
const TYPE_CONFIRM = 'confirm'
const TYPE_TIP = 'tip'

const TIP_SHOW_TIME = 1500

const popComp = {
  name: 'pop',

  render,

  mixins: [baseMixin],

  components: {
    btn: btnComp,
    icon: iconComp
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-pop`
    },
    isAlert() {
      return this.type === TYPE_ALERT
    },
    isTip() {
      return this.type === TYPE_TIP
    },
    //  stage  class 
    stageClass() {
      return [{
        [`${this.cPrefix}-tip-stage`]: this.isTip
      }, {
        [`${this.cPrefix}-alert-stage`]: this.isAlert
      }]
    },
    //  header  class 
    headerClass() {
      return { [`${this.cPrefix}-no-header-title`]: !this.popHeaderName }
    }
  },

  props: {
    type: {
      type: String,
      default: TYPE_CONFIRM
    },
    headerName: {
      type: String,
      default: ''
    },
    okBtnName: {
      type: String,
      default: ''
    },
    noBtnName: {
      type: String,
      default: ''
    },
    message: {
      type: String,
      default: ''
    },
    headerDisplay: {
      type: Boolean,
      default: true
    },
    headerNoBtnDisplay: {
      type: Boolean,
      default: true
    },
    noBtnDisplay: {
      type: Boolean,
      default: true
    },
    footerDisplay: {
      type: Boolean,
      default: true
    }
  },

  data: () => {
    return {
      pointStart: {
        x: 0,
        y: 0
      },
      isMousedown: false,
      popDisplay: false,
      popMessage: '',
      popHeaderName: '',
      okCb: 'undefined',
      onCb: 'undefined'
    }
  },

  methods: {
    /**
     * 
     */
    _setDataOpt() {
      this.popMessage = this.message
      this.popHeaderName = this.headerName
    },

    /**
     * pop
     *
     * @param {Number} - 
     * @return {Object}
     */
    show(cb) {
      if (this.isTip) {
        this.popDisplay = true
        cb && cb()

        setTimeout(() => {
          this.popDisplay = false

          if (this.okCb) {
            this.okCb()
          }
        }, TIP_SHOW_TIME)
      } else {
        this.popDisplay = true
      }

      return this
    },

    /**
     * pop
     *
     * @return {Object}
     */
    hide(event) {
      event && event.stopPropagation()

      if (this.isTip) {
        return this
      }

      this.popDisplay = false
      this.isMousedown = false

      return this
    },

    /**
     * mouseDown 
     *
     * @return {Object}
     */
    mouseDown(event) {
      this.isMousedown = true

      this.pointStart = {
        x: event.clientX,
        y: event.clientY
      }

      return this
    },

    /**
     * mouseMove 
     *
     * @return {Object, Boolean}
     */
    mouseMove(event) {
      if (!this.isMousedown) {
        return false
      }

      let $this = this.$el.querySelector('.' + this.xclass('stage'))
      let styleHub = getComputedStyle($this)
      let top = parseFloat(styleHub.top, 10)
      let left = parseFloat(styleHub.left, 10)

      $this.style.top = `${top + event.clientY - this.pointStart.y}px`
      $this.style.left = `${left + event.clientX - this.pointStart.x}px`

      this.pointStart = {
        x: event.clientX,
        y: event.clientY
      }

      return this
    },

    /**
     * mouseUp 
     *
     * @return {Object, Boolean}
     */
    mouseUp(event) {
      event.preventDefault()

      if (!this.isMousedown) {
        return false
      }

      this.isMousedown = false

      return this
    },

    /**
     * 
     *
     * @return {Object}
     */
    ok() {
      if (this.okCb) {
        this.okCb(this)

        return this.$emit('ok')
      }

      this.hide()
    },

    /**
     * 
     *
     * @return {Object}
     */
    cancel() {
      if (this.noCb) {
        this.noCb(this)

        return this.$emit('no')
      }

      this.hide()
    },

    /**
     * title
     *
     * @return {Object, Boolean}
     */
    title(text) {
      if (text === '' || text) {
        this.popHeaderName = text
      }

      return this
    },

    /**
     * alert, confirm 
     *
     * @param {String} - 
     * @return {Object, String}
     */
    info(text) {
      if (text === '' || text) {
        this.popMessage = text
      }

      return this
    },

    /**
     * alert, confirm 
     *  tip 
     *
     * @param {Function}
     * @return {Object}
     */
    setOkCb(cb) {
      this.okCb = cb

      return this
    },

    /**
     * alert, confirm 
     *
     * @param {Function}
     * @return {Object}
     */
    setNoCb(cb) {
      this.noCb = cb

      return this
    }
  }
}

export default popComp



