/**
 * btn 
 *
 * @props ban - 
 * @props flag - 
 * @props kind - 
 * @props link - 
 * @props size - 
 * @props submit - 
 * @props type -  (button | link)
 * @props value - 
 * @props textDisplay - 
 *
 * @events click - btn
 */

import './btn.scss'

import loadingComp from 'src/component/base/loading/loading'
import render from './btn.render'
import baseMixin from 'src/mixin/base'
import formMixin from 'src/mixin/form'

const BTN_TYPE_LINK = 'link'
const BTN_TYPE_BUTTON = 'button'

const SIZE_S = 'S'
const SIZE_M = 'M'
const SIZE_L = 'L'

const btnComp = {
  name: 'btn',

  mixins: [baseMixin, formMixin],

  render,

  components: {
    loading: loadingComp
  },

  props: {
    ban: {
      type: Boolean,
      default: false
    },

    flag: {
      type: String,
      require: false
    },

    link: Object,

    kind: {
      type: String,
      default: 'primary'
    },

    type: {
      type: String,
      default: BTN_TYPE_BUTTON
    },

    value: {
      type: String,
      require: true
    },

    size: {
      type: String,
      default: SIZE_S
    },

    submit: {
      type: Boolean,
      require: false
    },

    textDisplay: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      // 
      banState: false,
      // 
      btnValueDisplay: false,
      //  loading 
      createdLoading: false
    }
  },

  watch: {
    ban(val) {
      this.banState = val
    }
  },

  methods: {
    _setDataOpt() {
      this.banState = this.ban
    },

    /**
     * 
     * @return {Object} this - 
     */
    click() {
      return this.$emit('click')
    },

    /**
     * 
     */
    banBtn() {
      this.banState = true
    },

    /**
     * 
     */
    allowBtn() {
      this.banState = false
    },

    /**
     * 
     */
    openLoading() {
      if (!this.createdLoading) {
        this.createdLoading = true
        this.banBtn()
      }

      this.$nextTick(() => {
        this.$refs.loading.show()
      })
    },

    /**
     * 
     */
    classLoading(state) {
      this.allowBtn()
      this.$refs.loading.hide()
    }
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-btn`
    },

    btnClass() {
      if (!this.kind) {
        return false
      }

      return `ele-${this.kind}`
    },

    sizeClass() {
      return `ele-${this.size.toLowerCase()}`
    },

    isLink() {
      return !this.btnValueDisplay && this.type === BTN_TYPE_LINK
    },

    isButton() {
      return !this.btnValueDisplay && this.type === BTN_TYPE_BUTTON
    }
  }
}

export default btnComp



