/**
 * row 
 *
 * @props align -  flex  align-items 
 *    [start, end, center]
 * @props gap - px-- 
 * @props justify -  flex  justify-content 
 *    [start, end, center, justify]
 * @props wrap -  flex  flex-wrap nowrap | wrap
 * @props type - 
 *
 */

import './row.scss'
import render from './row.render.js'
import baseMixin from 'src/mixin/base'

const layoutType = ['grid', 'flex', 'flow']

export default {
  name: 'row',

  mixins: [baseMixin],

  render,

  props: {
    align: {
      type: String,
      default: 'center'
    },

    gap: {
      type: Number,
      default: 0
    },

    justify: {
      type: String,
      default: 'space-between'
    },

    wrap: {
      type: String,
      default: 'wrap'
    },

    type: {
      type: String,
      default: 'flow'
    }
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-row`
    },
    compClass() {
      let compClass = this.xclass([
        `align-${this.align}`,
        `justify-${this.justify}`,
        this.wrap
      ])

      return [compClass, this.cPrefix]
    }
  }
}



