/**
 * col 
 *
 * @props gap - px (5, 10, 20, 30, 40, 50)
 * @props pull -  x 
 * @props push -  x 
 * @props offset - 
 * @props span -  12 
 * @props xs - 
 * @props s - 
 * @props m - 
 * @props l - 
 * @props xl - 
 * @props grid - 
 *
 */

import './col.scss'
import render from './col.render.js'
import baseMixin from 'src/mixin/base'

export default {
  name: 'col',

  mixins: [baseMixin],

  render,

  props: {
    gap: {
      type: Number,
      default: 0
    },

    pull: {
      type: Number,
      default: 0
    },

    push: {
      type: Number,
      default: 0
    },

    offset: {
      type: Number,
      default: 0
    },

    span: {
      type: Number,
      default: 0
    },

    xs: {
      type: Number,
      default: 0
    },

    s: {
      type: Number,
      default: 0
    },

    m: {
      type: Number,
      default: 0
    },

    l: {
      type: Number,
      default: 0
    },

    xl: {
      type: Number,
      default: 0
    },

    grid: Object
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-col`
    }
  }
}



