/**
 * input 
 *
 * @props hidden - 
 * @props initVal - 
 * @props number -  nmuber 
 * @props placeholder - 
 * @props queryName - 
 * @props readOnly - 
 * @props row - textarea 
 * @props textLengthTip -
 * @props type - ( text | textarea )
 * @props theme - 
 *
 * @props empty - 
 * @props errorTipName - 
 * @props errorMessage - input 
 * @props errorTipType -  bubble | tip 
 * @props formatMessage - 
 * @props maxLength - inputtextarea 
 * @props regex - 
 * @props verifedType - 
 *
 * @props completion - 
 * @props completionUrl - url
 * @props completionItems - 
 * @props completionProcessor - 
 * @props completionKeyName -  key 
 *
 * @events change - inputBox
 * @events blur - inputBoxblur
 * @events focus - inputBoxfocus
 * @events keyup - inputBoxkeyup
 * @events inputBoxEvent.completion.click - 
 * @events inputBoxEvent.completion.change - 
 *
 */

import './input.scss'
import render from './input.render'

import store from 'src/vuex/store'
import hubStore from 'src/vuex/module/hub/type.json'
import initVerfication from './validate.js'

import baseMixin from 'src/mixin/base'
import formMixin from 'src/mixin/form'

import rowComp from 'src/component/common/layout/row/row'
import colComp from 'src/component/common/layout/col/col'

import { dataType } from 'src/util/data/data'

const tip = {}

const KEYUP_INTERVAL_TIME = 500
const TYPE_TEXT_AREA = 'textarea'
const TYPE_TEXT = 'text'
const ERROR_MESSAGE_TIP = 'tip'
const ERROR_MESSAGE_BUBBLE = 'bubble'

const inputComp = {
  name: 'input',

  render,

  mixins: [baseMixin, formMixin],

  components: {
    row: rowComp,
    column: colComp
  },

  store,

  props: {
    hidden: {
      type: Boolean,
      default: false
    },

    initVal: {
      type: [String, Number],
      default: ''
    },

    number: {
      type: Boolean,
      default: false
    },

    placeholder: {
      type: String,
      default: ''
    },

    queryName: {
      type: String,
      default: ''
    },

    errorTipName: String,

    readOnly: {
      type: Boolean,
      default: false
    },

    row: {
      type: Number,
      default: 4
    },

    textLengthTip: {
      type: Boolean,
      default: false
    },

    type: {
      type: String,
      default: 'text'
    },

    empty: {
      type: Boolean,
      default: true
    },

    errorMessage: {
      type: String,
      default: ''
    },

    errorTipType: {
      type: String,
      default: 'tip'
    },

    formatMessage: String,

    maxLength: Number,

    regex: String,

    verifedType: String,

    completion: {
      type: Boolean,
      default: false
    },

    completionItems: {
      type: Array,
      default: () => []
    },

    completionUrl: {
      type: String,
      default: ''
    },

    completionKeyName: {
      type: String,
      require: true
    },

    completionProcessor: Function
  },

  data() {
    return {
      // 
      compName: 'input',
      // 
      value: this.number ? this._switchNum(this.initVal) : this.initVal,
      //  focus 
      focusing: false,
      //  keyup 
      keyuping: false,
      // 
      dangerTip: '',
      // 
      completionDisplay: false,
      // 
      dataTypeName: '',
      // 
      verified: true,
      // 
      currentCompletionIndex: 'undefined',
      // 
      bubbleDisplay: false,
      // 
      inputTextLength: 0,
      staticCompletionItems: this.completionItems.slice()
    }
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-input`
    },
    // 
    _formatMessage() {
      return this.errorMessage ? this.errorMessage + '' : this.dataTypeName + ''
    },
    dangerTipDisplay() {
      return !!this.dangerTip && this.bubbleDisplay
    },
    isTextarea() {
      return this.type === TYPE_TEXT_AREA
    },
    isText() {
      return this.type === TYPE_TEXT
    },
    errorBorderDisplay() {
      return !this.verified
    },
    inputHub() {
      return this.$store.getters[hubStore.input.get]
    },
    //  stage  class 
    stageClass() {
      return [{
        [`${this.cPrefix}-textarea-stage`]: this.isTextarea
      }]
    },
    wrapClass() {
      return [
        this.xclass('wrap'),
        {
          [`${this.cPrefix}-editting`]: this.focusing
        },
        {
          [`${this.cPrefix}-error-border`]: this.errorBorderDisplay
        }
      ]
    },
    // input 
    inputBoxCol() {
      let slotHead = this.$slots.head
      let slotTail = this.$slots.tail

      if (slotHead && slotTail) {
        return 10
      } else if (slotHead || slotTail) {
        return 11
      } else {
        return 12
      }
    }
  },

  methods: {
    /**
     * 
     * @return {Object} this - 
     */
    _initVerfication() {
      if (this.regex) {
        this.regex = new RegExp(this.regex)

        return this
      }

      var verify = initVerfication(this.verifedType)

      if (verify) {
        this.regex = verify.regex
        this.dataTypeName = verify.dataTypeName
      }

      return this
    },

    /**
     * 
     * @return {Object} this - 
     */
    _initVerfiedMessage() {
      let errorMessage = this.errorMessage

      if (errorMessage) {
        this.emptyMessage = errorMessage
        this.lengthMessage = errorMessage

        return this
      }

      this.emptyMessage = this.errorMessage ? this.errorMessage : ''
      this.lengthMessage = this.errorMessage ? this.errorMessage : ''

      return this
    },

    /**
     * 
     * @return {Object} this - 
     */
    _fetchCompletion(key) {
      if (!this.completion) {
        this.completionDisplay = this.completionItems.length !== 0

        return false
      }

      if (this.completion && !this.completionUrl) {
        this.completionItems = this.staticCompletionItems.filter((item) => {
          return item.text.indexOf(key) > -1
        })

        this._processCompletion()
        this.completionDisplay = this.completionItems.length !== 0

        return
      }

      $.ajax({
        type: 'get',
        url: this.completionUrl,
        data: {
          [this.completionKeyName]: key
        },
        success: (result) => {
          if (result.code === 0) {
            this.completionItems = result.data
            this._processCompletion()
          } else {
            this.completionItems = []
          }

          this.completionDisplay = this.completionItems.length !== 0

          return this
        }
      })

      return this
    },

    /**
     *  value  change 
     * @return {Object} this - 
     */
    _dispatchChange() {
      return this.$emit('change', this.value)
    },

    /**
     * 
     *
     * @return {Object} - this - 
     */
    _verifyEmpty(firstVerify) {
      if (!this.empty) {
        if (this.bubbleDisplay) {
          this.dangerTip = firstVerify ? '' : `${this.emptyMessage}!`
        } else {
          this.dangerTip = `${this.emptyMessage}!`
        }
        this.verified = false
        return false
      }

      return true
    },

    /**
     * 
     *
     * @return {Object}
     */
    _clickCompletion(item, index) {
      this.value = item.text
      this.currentCompletionIndex = index

      return this
    },

    /**
     * 
     *
     * @return {Object}
     */
    _processCompletion() {
      if (!this.edit) {
        return this
      }

      this.completionProcessor && this.completionProcessor.call(null, this.completionItems)

      return this
    },

    /**
     * 
     *
     * @param {Boolean} - 
     * @return {Object} - this - 
     */
    verify(firstVerify) {
      this.value = $.trim(this.value)
      if (!this.value && this.value !== 0) {
        if (!this._verifyEmpty()) {
          this.verified = false

          // TODO bug
          // $(window).scrollTop($(this.$el).scrollTop())

          return false
        }

        this.verified = true
        this.dangerTip = ''

        return this
      } else {
        if (this.number && isNaN(this.value)) {
          this.dangerTip = `${this.errorMessage}`
          this.verified = false

          return false
        }

        if (this.maxLength) {
          if (this.value.toString().length > this.maxLength) {
            this.dangerTip = this.number
              ? `${this.lengthMessage}${this.maxLength}!`
              : `${this.lengthMessage}${this.maxLength}!`

            this.verified = false

            return false
          }
        }

        if (this.regex || this.verifedType) {
          if (!this.regex.test(this.value)) {
            this.dangerTip = firstVerify ? '' : this.formatMessage
            this.verified = false

            return false
          }
        }

        this.verified = true
        this.dangerTip = ''
        return this
      }
    },

    /**
     * 
     *
     * @return {Object} - this - 
     */
    validate() {
      this.verify()

      if (!this.verified) {
        tip(this.dangerTip)

        return false
      }

      return this
    },

    /**
     * text  value
     * @param {Number} - 
     */
    getCompletionItem(index = this.currentCompletionIndex) {
      if (this.currentCompletionIndex === 'undefined') {
        return 'undefined'
      }

      return this.completionItems[index]
    },

    /**
     *  focus 
     * @return {Object} this - 
     */
    focus(evt) {
      this.verified = true
      this.focusing = true

      return this.$emit('focus', {
        valeu: this.value,
        event: evt
      })
    },

    /**
     *  blur 
     * @return {Object} this - 
     */
    blur(evt) {
      this.focusing = false

      if (this.number) {
        this.value = this._switchNum(this.value)
      }

      return this.$emit('blur', {
        valeu: this.value,
        event: evt
      })
    },

    /**
     *  keyup 
     * @return {Object}
     */
    keyup() {
      if (this.keyuping) {
        return false
      }

      this.keyuping = true
      setTimeout(() => {
        this.keyuping = false
      }, KEYUP_INTERVAL_TIME)

      return this._fetchCompletion(this.value)
    },

    /**
     * 
     *
     * @return {Object}
     */
    fold() {
      this.completionDisplay = false

      return this
    },

    /**
     * 
     *
     * @return {Object}
     */
    spread() {
      this.completionDisplay = true

      return this
    },

    /**
     *  -  16 
     */
    _switchNum(val) {
      if (val === 0 || val === '0') {
        return 0
      }

      let strTemp = String(val)

      if (isNaN(strTemp)) {
        let temp = strTemp

        strTemp = strTemp.replace(/[^\d.]+/g, '')

        if (temp.indexOf('-') === 0) {
          strTemp = '-' + strTemp
        }
      }

      if (isNaN(strTemp)) {
        strTemp = ''
      }

      if (val.length >= 16) {
        return strTemp
      }

      return Number(strTemp)
    }
  },

  watch: {
    initVal(val, oldVal) {
      this.value = val
    },
    value(val, oldVal) {
      // 
      this.limitLen = String(val).length

      //  
      if ((this.completion && val !== '') ||
        Object.is(val, oldVal) || val === oldVal) {
        return false
      } else {
        this._dispatchChange()
        this.bubbleDisplay && this.verify()
      }
    }
  },

  created() {
    this.bubbleDisplay = this.errorTipType !== ERROR_MESSAGE_TIP
  },

  mounted() {
    this._initVerfication()
    this._initVerfiedMessage()

    this.$store.dispatch(hubStore.input.add, this)
  }
}

export default inputComp



